"use client";

import React from 'react';
import { JobOpening } from '@/shared/types';
import Link from 'next/link';

const Careers: React.FC = () => {
  const jobs: JobOpening[] = [
    { id: '1', title: 'Senior Product Designer', category: 'Design', type: 'Full-time', location: 'Remoto', isNew: true },
    { id: '2', title: 'Frontend Developer - React', category: 'Technology', type: 'Full-time', location: 'Híbrido' },
    { id: '3', title: 'Digital Marketing Specialist', category: 'Marketing', type: 'Full-time', location: 'Remoto' },
    { id: '4', title: 'Account Manager', category: 'Vendas', type: 'Full-time', location: 'Luanda' },
  ];

  return (
    <div className="overflow-x-hidden">
      {/* Hero Section */}
      <div className="relative pt-32 pb-20 lg:pt-48 lg:pb-32 overflow-hidden bg-[#0f172a]">
        {/* Background Elements */}
        <div className="absolute top-0 left-1/2 -translate-x-1/2 w-full h-full max-w-7xl pointer-events-none">
          <div className="absolute top-1/4 left-1/4 w-[400px] h-[400px] bg-green-500/20 rounded-full blur-[100px] animate-pulse-slow"></div>
          <div className="absolute bottom-0 right-0 w-[500px] h-[500px] bg-primary/20 rounded-full blur-[100px] animate-pulse-slow" style={{ animationDelay: '2s' }}></div>
        </div>

        <div className="absolute inset-0 bg-[url('/grid.svg')] bg-center [mask-image:linear-gradient(180deg,white,rgba(255,255,255,0))]"></div>

        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10 text-center">
          <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-white/5 border border-white/10 backdrop-blur-md text-white font-medium text-sm tracking-wider uppercase shadow-lg shadow-purple-500/10 mb-8">
            <span className="relative flex h-2 w-2">
              <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-green-400 opacity-75"></span>
              <span className="relative inline-flex rounded-full h-2 w-2 bg-green-500"></span>
            </span>
            Estamos Contratando
          </div>

          <h1 className="font-display font-black text-4xl md:text-6xl lg:text-7xl leading-tight text-white mb-8">
            Construa o Futuro <br className="hidden md:block" />
            <span className="text-transparent bg-clip-text bg-gradient-to-r from-[#8625d2] via-[#d946ef] to-[#10b981]">Conosco</span>
          </h1>

          <p className="text-lg md:text-xl text-gray-400 mb-12 max-w-2xl mx-auto leading-relaxed font-light">
            Buscamos mentes brilhantes e inquietas. Se você é apaixonado por inovação e quer impactar o mercado angolano, seu lugar é aqui.
          </p>

          <button
            onClick={() => document.getElementById('openings')?.scrollIntoView({ behavior: 'smooth' })}
            className="bg-white text-black px-8 py-4 rounded-xl font-bold text-lg shadow-[0_0_30px_rgba(255,255,255,0.3)] hover:scale-105 transition-transform"
          >
            Ver Vagas Abertas
          </button>
        </div>
      </div>

      {/* Culture / Benefits Section */}
      <section className="py-24 bg-white dark:bg-[#0b1120] relative">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
          <div className="mb-20 text-center">
            <span className="text-primary font-bold tracking-widest uppercase text-sm">Nossa Cultura</span>
            <h2 className="font-display font-black text-3xl md:text-4xl text-gray-900 dark:text-white mt-4">Mais que um emprego, uma missão</h2>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {[
              { icon: 'public', title: 'Remote First', desc: 'Liberdade para trabalhar de onde você se sente mais produtivo, seja em casa ou na praia.' },
              { icon: 'favorite', title: 'Saúde & Bem-estar', desc: 'Plano de saúde completo para você e seus dependentes, além de suporte psicológico.' },
              { icon: 'school', title: 'Evolução Contínua', desc: 'Orçamento anual dedicado exclusivamente para seus cursos, livros e eventos.' },
              { icon: 'payments', title: 'Salários Competitivos', desc: 'Remuneração compatível com o mercado internacional e bônus por performance.' },
              { icon: 'laptop_mac', title: 'Equipamentos de Ponta', desc: 'Macook ou Dell de última geração para você voar baixo no desenvolvimento.' },
              { icon: 'group', title: 'Ambiente Global', desc: 'Trabalhe com clientes de diversos setores e expanda seu networking.' }
            ].map((item, idx) => (
              <div key={idx} className="group p-8 rounded-[2rem] bg-gray-50 dark:bg-[#151e32] border border-gray-100 dark:border-white/5 hover:border-primary/50 transition-all duration-300 hover:-translate-y-2 hover:shadow-xl">
                <div className="w-14 h-14 bg-white dark:bg-white/5 rounded-2xl flex items-center justify-center mb-6 shadow-sm group-hover:scale-110 transition-transform">
                  <span className="material-icons-round text-3xl text-primary">{item.icon}</span>
                </div>
                <h3 className="font-display font-bold text-xl text-gray-900 dark:text-white mb-3">{item.title}</h3>
                <p className="text-sm text-gray-500 dark:text-gray-400 leading-relaxed">{item.desc}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Openings Section */}
      <section id="openings" className="py-24 bg-gray-50 dark:bg-[#0f172a] border-t border-gray-200 dark:border-white/5 relative">
        <div className="max-w-4xl mx-auto px-4 relative z-10">
          <div className="flex items-center justify-between mb-12">
            <h2 className="font-display font-black text-3xl md:text-4xl text-gray-900 dark:text-white">Vagas Disponíveis</h2>
            <span className="bg-primary/10 text-primary px-3 py-1 rounded-full text-xs font-bold uppercase">{jobs.length} Vagas</span>
          </div>

          <div className="space-y-4">
            {jobs.map(job => (
              <div key={job.id} className="group p-6 md:p-8 bg-white dark:bg-[#1e293b] border border-gray-100 dark:border-white/5 rounded-[1.5rem] hover:border-primary/50 hover:shadow-lg transition-all flex flex-col md:flex-row items-center justify-between cursor-pointer">
                <div className="flex flex-col gap-2 text-center md:text-left">
                  <div className="flex items-center gap-3 justify-center md:justify-start">
                    <h3 className="text-xl font-bold text-gray-900 dark:text-white group-hover:text-primary transition-colors">{job.title}</h3>
                    {job.isNew && <span className="bg-green-500 text-white text-[10px] font-bold px-2 py-0.5 rounded uppercase shadow-lg shadow-green-500/30">Novo</span>}
                  </div>
                  <div className="flex items-center gap-4 text-sm text-gray-500 dark:text-gray-400 mt-1 justify-center md:justify-start">
                    <span className="flex items-center gap-1 bg-gray-100 dark:bg-white/5 px-2 py-1 rounded-md"><span className="material-icons-round text-sm">folder</span> {job.category}</span>
                    <span className="flex items-center gap-1"><span className="material-icons-round text-sm">schedule</span> {job.type}</span>
                    <span className="flex items-center gap-1"><span className="material-icons-round text-sm">location_on</span> {job.location}</span>
                  </div>
                </div>

                <button className="mt-6 md:mt-0 bg-transparent border-2 border-gray-200 dark:border-gray-700 text-gray-700 dark:text-white px-6 py-2 rounded-xl font-bold hover:bg-primary hover:border-primary hover:text-white transition-all transform group-hover:translate-x-2">
                  Aplicar Agora
                </button>
              </div>
            ))}
          </div>

          <div className="mt-16 text-center">
            <p className="text-gray-500 dark:text-gray-400 mb-4">Não encontrou sua vaga?</p>
            <Link href="/contact" className="text-primary font-bold hover:underline">
              Envie seu currículo para nosso Banco de Talentos
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Careers;
